@font-face {
  font-family: "Roboto";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/Roboto-Light.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Bernier";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/BERNIERRegular-Regular.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Sansus";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/Sansus-Webissimo-Regular.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "LeviReBrushed";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/LeviReBrushed.woff2") format("woff2");
  font-display: swap;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

html,
body {
  height: 100%;
}

html {
  font-size: 16px;
}

body {
  font-family: "Roboto", Arial, sans-serif;
  color: #455a64;
  line-height: 1.42;
  position: relative;
  overflow-x: hidden;
  opacity: 1;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
  min-height: 100vh;
}

.wrapper {
  min-height: 100%;
  position: relative;
  overflow: hidden;
}

section {
  position: relative;
}

.visually-hidden:not(:focus):not(:active),
.svg-placeholder {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

.about {
  display: flex;
}

.about__left,
.about__right {
  flex: 1 1 50%;
  padding-bottom: 150px;
}

.about__left {
  background: #efeee9;
}

.about__right {
  background: #eeebdf;
}

.about__triangle,
.works-head__triangle {
  position: absolute;
  bottom: 100%;
}

.about__section--image {
  position: absolute;
  left: 40px;
  top: 0;
  fill: #6E7171;
  opacity: 0.07;
}

.about__section--wrapper {
  text-align: center;
  padding: 0 10%;
}

.user-about__text {
  text-align: left;
}

.about-user-info__title {
  text-align: center;
}

.about-user-info__text {
  padding-left: 40px;
}

.about-data__content {
  margin-top: 50px;
  padding: 0 10%;
}

.btn {
  padding: 10px 15px;
  border-radius: 5px;
  color: #fff;
  display: inline-block;
  text-decoration: none;
}

.mountains .btn {
  background-color: #00bfa5;
  transition: all 0.2s ease-in-out;
}

.mountains .btn:hover {
  background-color: #009688;
}

.down-arrow {
  bottom: 40px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.down-arrow--image {
  fill: #FFFFFF;
  opacity: 0.7;
}

.footer {
  padding: 0 5%;
  background-color: #373e42;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hamburger {
  position: absolute;
  right: 30px;
  top: 25px;
  display: inline-block;
  width: 40px;
  height: 35px;
}

.hamburger__image {
  fill: #fff;
}

.hero {
  height: 600px;
  overflow: hidden;
  color: #ffffff;
}

.hero__title {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  width: 100%;
  padding: 0 5%;
}

.hero__user-block {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero__bg--image {
  max-width: none;
}

.nav-menu__item {
  display: inline-block;
  padding: 25px 25px 25px 0;
}

.nav-menu__link {
  color: #ffffff;
  text-decoration: none;
  font-weight: 500;
  line-height: 24px;
  transition: all 0.2s ease-in-out;
}

.nav-menu__link:hover {
  color: #00bfa5;
}

.hero__title--image {
  opacity: 0.06;
  display: inline-block;
  fill: #FEFEFE;
}

.skill-section__title {
  font-size: 28px;
  font-weight: 500;
  line-height: 32px;
}

.skills__item {
  margin-bottom: 30px;
}

.skill-section__list {
  margin-top: 15px;
  display: flex;
}

.skill-section__item:nth-child(1n+2) {
  margin-left: 20px;
}

.skill {
  position: relative;
}

.skill__name {
  font-size: 13px;
  font-weight: 500;
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mountains .skill__name {
  color: #00bfa5;
}

.skill__circle {
  width: 100px;
  height: 100px;
  display: block;
  fill: transparent;
  stroke-width: 15px;
}

.skills__circle-under {
  stroke: #dfdcd5;
}

.mountains .skills__circle-above {
  stroke: #10c1a8;
}

.slider {
  display: flex;
  flex-flow: column wrap;
  height: 100vh;
}

.slider__desc {
  flex-basis: 100%;
  display: flex;
}

.slider__display {
  flex-grow: 4;
}

.slider__buttons {
  min-height: 100px;
  display: flex;
  flex-grow: 1;
}

.slider__btn {
  min-height: 100%;
  flex: 1;
}

.slider__btn--prev {
  background: url("../img/down.jpg") center center no-repeat;
  background-size: cover;
}

.slider__btn--next {
  background: url("../img/up.jpg") center center no-repeat;
  background-size: cover;
}

.slider__desc--title {
  margin-bottom: 20px;
}

.slider__desc--skills {
  padding: 20px 0;
  border-top: 1px solid  #009688;
  border-bottom: 1px solid #009688;
  color: #20b5a1;
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
  margin-bottom: 40px;
}

.slider__desc--btn {
  text-align: center;
  margin-bottom: 10px;
}

.slider__display--picture {
  max-width: 100%;
  display: block;
  max-height: 100%;
}

.slider__desc--info {
  margin: auto;
}

.socials {
  position: absolute;
  left: 30px;
  top: 30px;
  display: flex;
  align-items: center;
}

.socials__item {
  margin: 0 15px;
}

.socials__image {
  height: 22px;
  fill: #FFFFFF;
  opacity: .5;
  transition: all 0.2s ease-in-out;
}

.socials__image:hover {
  opacity: 1;
}

.socials__image--vk {
  width: 31px;
}

.socials__image--github {
  width: 26px;
}

.socials__image--in {
  width: 24px;
}

.socials__list {
  display: flex;
  align-items: center;
}

.socials__list .socials__link {
  display: inline-block;
  height: 35px;
}

.socials__list .socials__item {
  margin-left: 5px;
}

.socials__list .socials__image {
  width: 35px;
  height: 35px;
}

.title,
.subtitle {
  font-family: "Bernier",Arial,sans-serif;
  font-weight: 400;
  letter-spacing: 1.57px;
  position: relative;
  display: inline-block;
}

.title:after,
.subtitle:after {
  right: 40px;
}

.title:before,
.subtitle:before {
  left: 40px;
}

.title {
  font-size: 63px;
  padding: 45px 80px;
  color: #373e42;
  line-height: 66px;
}

.subtitle {
  color: #455a64;
  padding: 40px 80px;
  font-size: 35px;
  line-height: 41.01px;
}

.mountains .title:after,
.mountains .title:before,
.mountains .subtitle:after,
.mountains .subtitle:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 22px;
  height: 5px;
}

.mountains .title:after,
.mountains .title:before {
  background-color: rgba(55, 62, 66, 0.2);
}

.mountains .subtitle:after,
.mountains .subtitle:before {
  background-color: rgba(69, 90, 100, 0.2);
}

.triangle {
  height: 100px;
  width: 50%;
}

.triangle-fill-white {
  fill: #efeee9;
}

.triangle-fill-beige {
  fill: #eeebdf;
}

.user__photo img {
  border-radius: 50%;
}

.user__name {
  font-size: 35px;
  font-weight: 500;
  margin-top: 25px;
}

.user__desc {
  font-size: 16px;
  font-weight: 100;
  line-height: 24px;
  margin-top: 10px;
}

.user-about__text p:last-child {
  margin-top: 25px;
}

.works-head {
  text-align: center;
  background-color: #faf8f0;
}

.works-head__triangle {
  fill: #faf8f0;
}

.works-head__triangle:first-of-type {
  left: 0;
}

.works-head__triangle:last-of-type {
  right: 0;
}

@media screen and (max-width: 1200px) {
  html {
    font-size: 15px;
  }
}

@media screen and (max-width: 768px) {
  html {
    font-size: 14px;
  }
}

@media screen and (max-width: 480px) {
  html {
    font-size: 13px;
  }
}